import HeroMinimal from "@/components/hero-minimal";
import BrandsRow from "@/components/brands-row";
import Section from "@/components/section";
import ProjectCard from "@/components/project-card";
import { projects } from "@/lib/projects";

export default function Page() {
  return (
    <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-10 lg:py-14">
      <HeroMinimal />
      <BrandsRow />

      {/* Projeler bloğu (istenirse) */}
      <Section id="projects" title="Öne Çıkan Projeler" className="mt-12">
        <div className="grid sm:grid-cols-2 gap-6">
          {projects.slice(0,4).map((p) => (
            <ProjectCard key={p.slug} title={p.title} summary={p.summary} stack={p.stack} href={p.href}/>
          ))}
        </div>
      </Section>
    </div>
  );
}
