// components/project-card.tsx
import Link from "next/link";

export default function ProjectCard({
  title, summary, stack, href, slug
}: { title:string; summary:string; stack:string[]; href?:string; slug?:string }) {
  return (
    <div className="rounded-2xl p-6 bg-[--card] shadow-lg border border-white/5 hover:border-white/10 transition">
      <h3 className="text-white font-semibold text-lg">{title}</h3>
      <p className="text-gray-300 mt-2">{summary}</p>
      <div className="mt-3 flex flex-wrap gap-2">
        {stack.map((s) => (
          <span key={s} className="text-xs px-2 py-1 rounded-full bg-white/5 text-gray-300 border border-white/10">{s}</span>
        ))}
      </div>
      <div className="mt-4 flex gap-4 text-sm">
        {slug && <Link href={`/projects/${slug}`} className="text-emerald-400 hover:underline">Detay</Link>}
        {href && <a href={href} target="_blank" className="text-emerald-400 hover:underline">Kaynak</a>}
      </div>
    </div>
  );
}
